const getStatusBadge = (status: string) => {
  switch (status) {
    case 'Active':
      return <Badge className="bg-green-500/20 text-green-400 border-green-500/30">Active</Badge>
    case 'Under Maintenance':
      return <Badge className="bg-yellow-500/20 text-yellow-400 border-yellow-500/30">Under Maintenance</Badge>
    case 'Archived':
      return <Badge className="bg-red-500/20 text-red-400 border-red-500/30">Archived</Badge>
    default:
      return <Badge className="bg-slate-500/20 text-slate-400 border-slate-500/30">{status}</Badge>
  }
}

const getTypeBadge = (type: string) => {
  switch (type) {
    case 'Residential':
      return <Badge className="bg-blue-500/20 text-blue-400 border-blue-500/30">Residential</Badge>
    case 'Commercial':
      return <Badge className="bg-purple-500/20 text-purple-400 border-purple-500/30">Commercial</Badge>
    default:
      return <Badge className="bg-slate-500/20 text-slate-400 border-slate-500/30">{type}</Badge>
  }
}

